<template>
  <div
    class="flex items-center justify-center flex-col"
    style="min-height: 300px"
  >
    <div
      class="flex items-center p-2 bg-red-lighten text-red-04 rounded-lg w-max my-6"
    >
      <i class="material-icons mx-2">info</i>
      <span class="mx-2"
        >There are no shipping couriers enabled on
        {{ shippingType }} account</span
      >
    </div>
    <Button color="teal" view="smooth" @click="handleSetupAccount"
      >Set Up Account</Button
    >
  </div>
</template>
<script>
import { defineComponent } from 'vue'

import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  components: {
    Button
  },
  props: {
    shippingType: {
      type: String,
      default: 'Shipmondo'
    }
  },
  emits: ['action'],
  setup(_, { emit }) {
    const handleSetupAccount = () => {
      emit('action', true)
    }
    return {
      handleSetupAccount
    }
  }
})
</script>
